@import url('https://fonts.googleapis.com/css2?family=Lora&family=Montserrat&family=Nova+Square&display=swap');

*{
    margin: 0;
}
.container{
   background: url(./1428482.jpg);
    height: 100vh;
    background-repeat: no-repeat;
    background-size: cover;
    margin-top: 0;
   

}

.main{
    position: relative;
    color: white;
}

.navbar{
    display: flex;
    flex-direction: row;
    position: sticky;
    top: 0; 
    
    

    
}
.navbar h1{
    padding-left: 10px;
    padding-top: 10px;
    font-family: 'Nova Square', sans-serif;
    
   

}
.navbar span{
    color: rgb(40, 40, 235);
}

.navbar li{
    list-style-type: none;
    font-size: 20px;
    padding-top: 17px;
    font-weight: 500;
    margin-left: 10em;

    
}

.navbar li:hover{
    cursor: pointer;
    color: rgb(99, 99, 230);
}


.middle_platform h1{

    display: flex;
    margin-left: 1em;
    margin-top: 2em;
    font-size: 75px;
    width: 7em;
    animation: slide 0.5s linear forwards;
    

}


@media all and (max-width: 391px) and (max-height: 845px) {


.navbar h1{
    font-size: 20px;
    margin-left: 10px;
}

.middle_platform h1{
    font-size: 30px;
}




.container{
    height: 50vh;
}

















}