@import url('https://fonts.googleapis.com/css2?family=Lora:wght@400;500&family=Montserrat&family=Nova+Square&display=swap');


html{
    scroll-behavior: smooth;
}
.container_{
    
    height: 670vh;
    background: rgb(72,45,195);
    background: linear-gradient(22deg, rgba(72,45,195,1) 20%, rgba(183,6,201,1) 40%, rgba(135,45,195,1) 60%, rgba(76,45,195,1) 80%);
    display: flex;
    justify-content: center;
    
    
}

.navBar{
    width: 60em;
    padding: 10px;
    border-radius: 20px;
    display: flex;
    flex-direction: row;
    list-style: none;
    background-color: rgb(72, 72, 121);
    top: 0 ;
    position: sticky;
    z-index: 99;
    animation: fade linear;
   
}

.navBar a{
    color: white;
    text-decoration: none;
    
}

.navBar a:hover{
    color: grey;
}

.main{
    position: relative;
}
.navBar li{
   margin-left: 5em;
   font-weight: bolder;
   font-size: 25px;
}

p{
    font-style: 'Lora';
    font-family: 'Lora', serif;
font-family: 'Montserrat', sans-serif;
font-family: 'Nova Square', sans-serif;
animation: fade linear;
animation-timeline: view();
}

h1{
    animation: fade linear;
    animation-timeline: view();
}

.container_ h1 {
    margin-top: 5em;
    margin-bottom: 2em;
    color: white;
    font-size: 35px;
    display: flex;
    justify-content: center;
    animation: fade linear;
    animation-timeline: view();
    
}

iframe{
    border-radius: 10px;
    box-shadow: 1px 1px 1pc black;
    animation: fade linear ;
    animation-timeline: view();
    margin-top: 10em;
    margin-left: 10em;
    position: relative;
    width: 760px;
    height: 415px;
    
}


.first{
    width: 50em;
    margin-left: 10px;
    font-size: 20px;
    animation: fade linear;
    animation-timeline: view();

}



.first span{
    color: aqua;
    font-size: 23px;
}

.card_{
    margin-top: 8em;
    list-style: none;
    display: flex;
    justify-content: center;
    
    animation: slide linear;
    animation-timeline: view();

    
    
}



.card_ li{
    margin: 20px;
    font-size: 35px;
}

.card_ span{
    color:aqua;
    font-weight: bolder;
    font-size: 36px;
}


.second{
    margin-top: 2em;
    display: flex;
    justify-content: center;
}

.second p{
    width: 40em;
    font-size: 20px;
}


.third p{
    font-size: 20px;
    width: 50em;
}

.image{
    background-image: url('./Linked-List-Data-Structure.png');
    width: 40em;
    height: 20em;
    background-repeat: no-repeat;
    background-size: contain;
    margin-top: 10em;
    border-radius: 10px;
    box-shadow: 1px 1px 1pc black;
    margin-left: 12em;
    

    
}

.image_1{
    background-image: url('./bubble-sort-1024x683-2-3281434158.png');
    width: 30em;
    height: 20em;
    background-size: contain;
    margin-top: 10em;
    border-radius: 10px;
    box-shadow: 1px 1px 1pc black;
    margin-left: 17em;
    
  
}

.image-2{
    background-image: url('./binary-tree-1-pre-order-3862689416.gif');
    width: 35em;
    height: 25em;
    background-size:contain;
    margin-top: 10em;
    border-radius: 10px;
    box-shadow: 1px 1px 1pc black;
    margin-left: 15em;
  
}

#span{
    animation: stand;

}

@keyframes stand{
    from{
        color: bisque;
    }

    to {
        color: aliceblue;
    }
}





















@keyframes fade {
    from {
        opacity: 0;
    } 
    to {
        opacity: 1;
    }
    0%{
        opacity: 0;
    }
    50%{
        opacity: 1;
    }

    100%{
        opacity: 1;
    }
    
}

@keyframes slide {
    from {
        transform: translateX(-30%)
    } 
    to{
        transform: translateX(100);
    }
}



@media all and (max-width: 391px) and (max-height: 845px) {
    #linkedlist {
        font-size: 25px;
        
    }

    #binarytree{
        font-size: 25px;
    }

    #bubblesort{
        font-size: 25px;
    }

    .second p{
        width: 20em;
        font-size: 15px;

    }

    .image-2{
        width: 270px;
        height: 210px;
        background-size: contain;
        background-repeat: no-repeat;
        margin-left: 367px;
    }
    iframe{
        width: 290px;
        height: 220px;
        margin-left: 362px;
    }

    .card_ li{
        font-size: 14px;
        margin-left: 0;
        margin-right: 5px;
    }

    .card_ span{
        font-size: 18px;
    }
    .card_{
        display: flex;
        justify-content: center;
        align-items: center;
        margin-left: 10px;
        animation: fade linear;
        animation-timeline: view();
    }
    .image{
        width: 280px;
        height: 145px;
        background-size: contain;
        background-repeat: no-repeat;
        margin-left: 364px;
    }
    .third p{
        width: 20em;
        font-size: 15px;
        margin-left: 360px;

    }
    .first p{
        width: 20em;
        font-size: 15px;
        margin-left: 360px;

    }

    .image_1{
        width: 280px;
        height: 210px;
        background-size: contain;
        background-repeat: no-repeat;
        margin-left: 364px;
    }
    .navBar{
        
        width: 10px;
        
        
    }
    .navBar a{
        font-size: 10px;
        margin-left: 2px;
        margin-right: 2px;
        
    }
}